import React, { Component } from 'react'
import Fade from "react-reveal/Fade";
import { Navbar, Image } from 'react-bootstrap';
import './Footer.css';
import Fb from '../../../assets/images/social-logos/facebook.svg';
import Tw from '../../../assets/images/social-logos/twitter.svg';
import Yt from '../../../assets/images/social-logos/youtube.svg';
import Li from '../../../assets/images/social-logos/linkedin.svg';

import BoostedFooterLogo from '../../../assets/images/boosted-logo.svg';

export default class Footer extends Component {
    render() {
        return (
            <div className="footer-parent">
                <Navbar className="global-footer">
                    <Fade bottom duration={1000} distance={"20px"}>
                        <div className="footer-container container">

                            <Image className="footer-logo" src={BoostedFooterLogo} />
                            <div>
                                Resume  <br /><br />
                                <p className="sub-headings">
                                    <a style={style.footerLink} href="/resume">Build Now</a><br />
                                    <a style={style.footerLink} href="/resume" disabled><strike>Existing User</strike></a>
                                </p>
                            </div>
                            <div>
                                Our Work <br /><br />

                                <p className="sub-headings">
                                    <a style={style.footerLink} href='/our-work'>Communications</a><br />
                                    <a style={style.footerLink} href='/our-work'>Design</a><br />
                                    <a style={style.footerLink} href='/our-work'>Development</a><br />
                                    <a style={style.footerLink} href='/our-work'>Studio</a><br />
                                    <a style={style.footerLink} href='/our-work'>Sales X</a><br />
                                    <a style={style.footerLink} href='/our-work'>Nitro</a>
                                </p>
                            </div>
                            <div>
                                Our Story <br /><br />
                                <p className="sub-headings">
                                    <a style={style.footerLink} href='/our-story'>About Us</a><br />
                                    <a style={style.footerLink} href='/contact-us'>Contact Us</a><br />
                                    <a style={style.footerLink} href='/highlights'>Highlights</a><br />
                                    <a style={style.footerLink} href='http://boostedblog.in'>Blog</a><br />
                                </p>
                            </div>
                            <div>
                                Connect With Us <br /><br />
                                <div>
                                    <Image src={Fb} />
                                    <Image style={{ 'marginLeft': '12px' }} src={Li} />
                                    <Image style={{ 'marginLeft': '12px' }} src={Tw} />
                                    <Image style={{ 'marginLeft': '12px' }} src={Yt} />
                                </div><br />

                                Address<br /><br />
                                <p className="sub-headings">
                                    5th Floor, Plot #682, Madhapur<br />
                                    Hyderabad, Telangana, India
                                </p>
                            </div>
                        </div>
                    </Fade>
                </Navbar>
                <div className="footer-credits">
                    © BOOSTED Springboard. ALL RIGHTS RESERVED
                </div>
            </div>
        )
    }
}

const style = {
    footerLink: {
        textDecoration: 'none',
        color: 'white'
    }
};