import React, { Component } from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import './App.css';
import MainPage from './components/MainPage/MainPage';
import RegisterPage from './components/RegisterPage/RegisterPage';
import Root from './components/Root/Root';
import { connect } from 'react-redux';
import * as actions from './store/actions/auth';

import ResumeOne from './resume-templates/resume17/resume17';
import ResumeTwo from './resume-templates/resumeTwo/resumeTwo';
import ResumeThree from './resume-templates/resumeThree/resumeThree';
import ResumeFour from './resume-templates/resumeFour/resumeFour';
import ResumeFive from './resume-templates/resumeFive/resumeFive';
import ResumeSix from './resume-templates/resumeSix/resumeSix';
import ResumeSeven from './resume-templates/resumeSeven/resumeSeven';
import ResumeEight from './resume-templates/resumeEight/resumeEight';
import ResumeNine from './resume-templates/resumeNine/resumeNine';
import ResumeTen from './resume-templates/resumeTen/resumeTen';
import Resume11 from './resume-templates/resume11/resume11';
import Resume12 from './resume-templates/resume12/resume12';
import Resume13 from './resume-templates/resume13/resume13';
import Resume14 from './resume-templates/resume14/resume14';
import Resume15 from './resume-templates/resume15/resume15';
import Resume17 from './resume-templates/resumeOne/resumeOne';

import CustomHeader from './editorComponents/customHeader';

class App extends Component {

  componentDidMount() {
    this.props.onTryAutoSignup();
  }

  render() {
    return (
      <Router>
        <Route path={"/"} exact render={() => <Root><MainPage /></Root>} />
        <Route path={"/register"} exact render={() => <Root><RegisterPage /></Root>} />
        {/* Routes for Resume Templates */}
        <Route path={"/resume-edit-1"} render={() => <CustomHeader><ResumeOne /></CustomHeader>} />
        <Route path={"/resume-edit-2"} render={() => <CustomHeader><ResumeTwo /></CustomHeader>} />
        <Route path={"/resume-edit-3"} render={() => <CustomHeader><ResumeThree /></CustomHeader>} />
        <Route path={"/resume-edit-4"} render={() => <CustomHeader><ResumeFour /></CustomHeader>} />
        <Route path={"/resume-edit-5"} render={() => <CustomHeader><ResumeFive /></CustomHeader>} />
        <Route path={"/resume-edit-6"} render={() => <CustomHeader><ResumeSix /></CustomHeader>} />
        <Route path={"/resume-edit-7"} render={() => <CustomHeader><ResumeSeven /></CustomHeader>} />
        <Route path={"/resume-edit-8"} render={() => <CustomHeader><ResumeEight /></CustomHeader>} />
        <Route path={"/resume-edit-9"} render={() => <CustomHeader><ResumeNine /></CustomHeader>} />
        <Route path={"/resume-edit-10"} render={() => <CustomHeader><ResumeTen /></CustomHeader>} />
        <Route path={"/resume-edit-11"} render={() => <CustomHeader><Resume11 /></CustomHeader>} />
        <Route path={"/resume-edit-12"} render={() => <CustomHeader><Resume12 /></CustomHeader>} />
        <Route path={"/resume-edit-13"} render={() => <CustomHeader><Resume13 /></CustomHeader>} />
        <Route path={"/resume-edit-14"} render={() => <CustomHeader><Resume14 /></CustomHeader>} />
        <Route path={"/resume-edit-15"} render={() => <CustomHeader><Resume15 /></CustomHeader>} />
        <Route path={"/resume-edit-17"} render={() => <CustomHeader><Resume17 /></CustomHeader>} />
      </Router>
    );
  }
}


const mapStateToProps = state => {
  return {
    isAuthenticated: state.token !== null
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onTryAutoSignup: () => dispatch(actions.authCheckState())
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);